import React, { useState } from "react";
import styled, { css } from "styled-components";
import Input from "../Shared/Input";
import Label from "../Shared/Label";
import Title from "../Shared/Title";
import { Button } from "../Shared/Button";
import { ReactComponent as VisibleIcon } from "../../assets/pw_visisble.svg";
import { ReactComponent as HiddenIcon } from "../../assets/pw_hidden.svg";

enum InputType {
  USERNAME = "username",
  PASSWORD = "password",
}

const StyledLoginWidget = styled.form`
  width: 100%;
  padding: 15px;
`;

const InputWrapper = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 30px;
  margin-bottom: 10px;
  border-radius: 7px;
`;

const IconWrapper = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 25px;
  width: 25px;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 15px;
  background-color: transparent;

  &:hover {
    background-color: ${({ theme }) => theme.main_bg};
  }

  & svg {
    height: 80%;
    width: 80%;
  }
`;

const FormFooter = styled.div`
  width: 100%;
  padding: 5px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const FooterButton = styled(Button)`
  margin: 0;
  ${({ primary }) =>
    primary &&
    css`
      border: none;
      background-color: ${({ theme }) => theme.green};
      color: ${({ theme }) => theme.black};
    `}
`;

const LoginWidget: React.FC<{
  toggleCurrentView: () => void;
}> = ({ toggleCurrentView }) => {
  const [usernameInput, setUsernameInput] = useState("");
  const [passwordInput, setPasswordInput] = useState("");
  const [passwordVisible, setPasswordVisible] = useState(false);

  const handleLoginSubmit = () => {
    console.log(usernameInput);
  };

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    switch (e.target.name) {
      case InputType.USERNAME:
        setUsernameInput(e.target.value);
        break;
      case InputType.PASSWORD:
        setPasswordInput(e.target.value);
        break;
      default:
        return;
    }
  };

  return (
    <StyledLoginWidget onSubmit={e => e.preventDefault()}>
      <Title>Login</Title>
      <Label>{InputType.USERNAME}</Label>
      <InputWrapper>
        <Input
          type="text"
          name={InputType.USERNAME}
          value={usernameInput}
          onChange={handleInputChange}
          placeholder="johnyb04"
        />
      </InputWrapper>
      <Label>{InputType.PASSWORD}</Label>
      <InputWrapper>
        <Input
          type={passwordVisible ? "text" : "password"}
          name={InputType.PASSWORD}
          value={passwordInput}
          onChange={handleInputChange}
          placeholder="********"
        />
        <IconWrapper>
          {passwordVisible ? (
            <VisibleIcon onClick={() => setPasswordVisible(false)} />
          ) : (
            <HiddenIcon onClick={() => setPasswordVisible(true)} />
          )}
        </IconWrapper>
      </InputWrapper>
      <FormFooter>
        <FooterButton onClick={toggleCurrentView}>Register</FooterButton>
        <FooterButton primary onClick={handleLoginSubmit}>
          Submit
        </FooterButton>
      </FormFooter>
    </StyledLoginWidget>
  );
};

export default LoginWidget;
