import { useState, useEffect } from "react";

type HookReturnType = {
  loading: boolean;
  words: string[];
  error: any;
};

export const useFetchWords = (tryCount: number): HookReturnType => {
  const [loading, setLoading] = useState(false);
  const [words, setWords] = useState([]);
  const [error, setError] = useState(null);

  useEffect(() => {
    setWords([]);
    const fetchWords = async (): Promise<any> => {
      setLoading(true);
      try {
        const fetchedWords: any = await fetch(
          "https://random-word-api.herokuapp.com/word?number=10000"
        );
        const parsedWords = await fetchedWords.json();
        setWords(
          parsedWords.filter(
            (w: string, i: number) => w.length > 3 && w.length < 15 && i <= 70
          )
        );
      } catch (fetchError) {
        setError(fetchError);
      } finally {
        setLoading(false);
      }
    };
    fetchWords();
  }, [tryCount]);

  return { loading, words, error };
};
