import React, { useState, useRef } from "react";
import styled from "styled-components";
import LogoWrapper from "./LogoWrapper";
import NavBar from "./NavBar";
import UserAuthentication from "../UserAuthentication";

const StyledHeader = styled.header`
  position: relative;
  height: 66px;
  background-color: ${({ theme }) => theme.blue};
  box-shadow: ${({ theme }) => theme.box_shadow_lg};
  padding: 0 calc(20px + 0.5vw);
  display: flex;
  align-items: center;
  justify-content: space-between;
  border-radius: 6px;
`;

const Header: React.FC = () => {
  const [showUserAuth, setShowUserAuth] = useState(false);
  const [showUserAdmin, setShowUserAdmin] = useState(false);
  const navBarRef = useRef<HTMLHeadElement>(null);

  const toggleShowUserAuth = () => {
    setShowUserAuth(!showUserAuth);
  };

  // const toggleShowUserAdmin = () => {
  //   if (showUserAuth) {
  //     setShowUserAuth(false);
  //   }
  //   setShowUserAdmin(!showUserAdmin);
  // };

  return (
    <StyledHeader ref={navBarRef}>
      <LogoWrapper />
      <NavBar toggleAuth={toggleShowUserAuth} toggleAdmin={toggleShowUserAuth} />
      {showUserAuth && (
        <UserAuthentication
          handleClickOutside={toggleShowUserAuth}
          navBarRef={navBarRef}
        />
      )}
    </StyledHeader>
  );
};

export default Header;
