import { createContext } from "react";

export interface IUserData {
  fullName: string;
  username: string;
  password: string;
  email: string;
  token: string;
}

export type LoginData = Pick<IUserData, "username" | "password">;

export type RegisterData = Omit<IUserData, "token">;

export type ContextType = {
  isLoggedIn: boolean;
  userData: IUserData | null;
  registerUser: (registerData: RegisterData) => void;
  loginUser: (loginData: LoginData) => void;
  logoutUser: () => void;
};

const defaultValue = {
  isLoggedIn: false,
  userData: null,
  registerUser: () => {},
  loginUser: () => {},
  logoutUser: () => {},
};

export const UserContext = createContext<ContextType>(defaultValue);
