import React, { useState, useContext } from "react";
import styled, { css } from "styled-components";
import { useForm } from "react-hook-form";
import { UserContext } from "../../context/userContext";
import Input from "../Shared/Input";
import Label from "../Shared/Label";
import Title from "../Shared/Title";
import { Button } from "../Shared/Button";

enum InputType {
  FULL_NAME = "full name",
  EMAIL = "email",
  USERNAME = "username",
  PASSWORD = "password",
}

const StyledRegisterWidget = styled.form`
  width: 100%;
  padding: 15px;
`;

const InputWrapper = styled.div`
  position: relative;
  overflow: hidden;
  width: 100%;
  height: 30px;
  margin-bottom: 10px;
  border-radius: 7px;
`;

const IconWrapper = styled.div`
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  cursor: pointer;
  height: 25px;
  width: 25px;
  right: 5px;
  top: 50%;
  transform: translateY(-50%);
  border-radius: 15px;
  background-color: transparent;

  &:hover {
    background-color: ${({ theme }) => theme.main_bg};
  }

  & svg {
    height: 80%;
    width: 80%;
  }
`;

const FormFooter = styled.div`
  width: 100%;
  padding: 5px 0;
  display: flex;
  align-items: center;
  justify-content: space-between;
`;

const FooterButton = styled(Button)`
  margin: 0;
  ${({ primary }) =>
    primary &&
    css`
      border: none;
      background-color: ${({ theme }) => theme.green};
      color: ${({ theme }) => theme.black};
    `}
`;

type FormTypes = {
  fullName: string;
  email: string;
  username: string;
  password: string;
};

const RegisterWidget: React.FC<{
  toggleCurrentView: () => void;
}> = ({ toggleCurrentView }) => {
  const [fullName, setFullName] = useState("");
  const [email, setEmail] = useState("");
  const [username, setUsername] = useState("");
  const [password, setPassword] = useState("");
  const { handleSubmit, register, errors } = useForm<FormTypes>();
  const { registerUser } = useContext(UserContext);

  const handleInputChange = (e: React.ChangeEvent<HTMLInputElement>) => {
    const { name, value } = e.target;
    switch (name) {
      case InputType.FULL_NAME:
        setFullName(value);
        break;
      case InputType.EMAIL:
        setEmail(value);
        break;
      case InputType.USERNAME:
        setUsername(value);
        break;
      case InputType.PASSWORD:
        setPassword(value);
        break;
      default:
        return;
    }
  };

  const handleLoginSubmit = () => {
    try {
      handleSubmit(() => registerUser({ fullName, username, email, password }));
    } catch (error) {
      console.log("error");
    }
  };

  return (
    <StyledRegisterWidget onSubmit={e => e.preventDefault()}>
      <Title>Register</Title>
      <Label>{InputType.FULL_NAME}</Label>
      <InputWrapper>
        <Input
          type="text"
          placeholder="John Doe"
          value={fullName}
          onChange={handleInputChange}
          name={InputType.FULL_NAME}
          ref={register({
            required: "required",
            pattern: {
              value: new RegExp("^[a-zA-Z]{4,}(?: [a-zA-Z]+){0,2}$"),
              message: "ivalid input",
            },
          })}
        />
      </InputWrapper>
      <Label>{InputType.EMAIL}</Label>
      <InputWrapper>
        <Input
          type="text"
          placeholder="johndoe@somemail.com"
          value={email}
          onChange={handleInputChange}
          name={InputType.EMAIL}
          ref={register({
            required: "required",
            pattern: {
              value: new RegExp("^w+@[a-zA-Z_]+?.[a-zA-Z]{2,3}$"),
              message: "ivalid input",
            },
          })}
        />
      </InputWrapper>
      <Label>{InputType.USERNAME}</Label>
      <InputWrapper>
        <Input
          type="text"
          placeholder="johnyboiii"
          value={username}
          onChange={handleInputChange}
          name={InputType.USERNAME}
          ref={register({
            required: "required",
            pattern: {
              value: new RegExp("^(?=[a-zA-Z0-9._]{8,20}$)(?!.*[_.]{2})[^_.].*[^_.]$"),
              message: "ivalid input",
            },
          })}
        />
      </InputWrapper>
      <Label>{InputType.PASSWORD}</Label>
      <InputWrapper>
        <Input
          type="text"
          placeholder="Somepw123+"
          value={password}
          onChange={handleInputChange}
          name={InputType.PASSWORD}
          ref={register({
            required: "required",
            pattern: {
              value: new RegExp(
                "^(?=.*[a-z])(?=.*[A-Z])(?=.*d)(?=.*[@$!%*?&])[A-Za-zd@$!%*?&]{8,}$"
              ),
              message: "ivalid password",
            },
          })}
        />
      </InputWrapper>
      <FormFooter>
        <FooterButton onClick={toggleCurrentView}>Login</FooterButton>
        <FooterButton primary onClick={handleLoginSubmit}>
          Submit
        </FooterButton>
      </FormFooter>
    </StyledRegisterWidget>
  );
};

export default RegisterWidget;
